@font-face {
  font-display: swap;
  font-family: 'Font Awesome 5 Free';
  font-style: normal;
  font-weight: 900;
  src: url('https://use.fontawesome.com/releases/v5.2.0/webfonts/fa-solid-900.eot');
  src: url('https://use.fontawesome.com/releases/v5.2.0/webfonts/fa-solid-900.eot?#iefix')
      format('embedded-opentype'),
    url('https://use.fontawesome.com/releases/v5.2.0/webfonts/fa-solid-900.woff2') format('woff2'),
    url('https://use.fontawesome.com/releases/v5.2.0/webfonts/fa-solid-900.woff') format('woff'),
    url('https://use.fontawesome.com/releases/v5.2.0/webfonts/fa-solid-900.ttf') format('truetype'),
    url('https://use.fontawesome.com/releases/v5.2.0/webfonts/fa-solid-900.svg#fontawesome')
      format('svg');
}

.fa,
.far,
.fas {
  font-family: 'Font Awesome 5 Free';
  font-style: normal;
}

.fa,
.fas {
  font-weight: 900;
}

.fa-home:before {
  content: '\f015';
}

.fa-trophy:before {
  content: '\f091';
}

.fa-graduation-cap:before {
  content: '\f19d';
}

.fa-envelope:before {
  content: '\f0e0';
}
